import React, { useState, useEffect } from "react";
import { dbService } from "myBase";
import Nweet from "components/Nweet";
import NweetFactory from "components/NweetFactory";

const Home = ({ userObj }) => {
    const [nweets, setNweets] = useState([]);

    const onSnapShot = () => {
        dbService.collection("nweet").onSnapshot((snapshot) => {
            /* 
            TODO: update array with changes only instead of replacing the array with new array
            */
            const nweetArray = snapshot.docs.map((document) => ({
                id: document.id,
                ...document.data(),
            }))
            setNweets(nweetArray);
        })
    }

    useEffect(() => {
        onSnapShot();
    }, []);

    return (
        <div className="container">
            <NweetFactory userObj={userObj} />
            <div>
                {nweets.map((nweet) => (
                    <Nweet key={nweet.id} nweet={nweet} isOwner={userObj.uid === nweet.creatorID} />
                ))}
            </div>
        </div>
    );
}
export default Home;