import { authService } from "myBase";
import React, { useState } from "react";

const AuthForm = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [newAccount, setNewAcount] = useState(false);
    const [error, setError] = useState("");

    const onChange = (e) => {
        const { target: { name, value } } = e;
        if (name === "email") {
            setEmail(value);
        } else if (name === "password") {
            setPassword(value);
        }
    };

    const onSubmit = async (e) => {
        e.preventDefault();
        try {
            let data;
            if (newAccount) {
                //create account
                data = await authService.createUserWithEmailAndPassword(
                    email, password
                );
            } else {
                //log in
                data = await authService.signInWithEmailAndPassword(
                    email, password
                );
            }
            console.log(data);
        } catch (error) {
            setError(error.message);
        }
    };

    const toggleSignInBtn = () => {
        setNewAcount(prev => !prev);
    }

    return (
        <>
            <form onSubmit={onSubmit} className="container">
                <input className="authInput" onChange={onChange} type="text" placeholder="Email" name="email" required value={email} />
                <input className="authInput" onChange={onChange} type="password" placeholder="Password" name="password" required value={password} />
                <input className="authInput authSubmit" type="submit" value={newAccount ? "create account" : "sign in"} />
                {error &&
                    <span className="authError">{error}</span>
                }
            </form>
            <span className="authSwitch" onClick={toggleSignInBtn} >{newAccount ? "sign in" : "create account"}</span>
        </>
    )
}

export default AuthForm;