import React, { useEffect, useState } from "react";
import { authService, dbService } from "myBase";
import { useHistory } from "react-router-dom";

const Profile = ({ userObj }) => {
    const history = useHistory();
    const onLogoutClick = () => {
        authService.signOut();
        history.push("/");
    }

    const [displayName, setDisplayName] = useState(userObj.displayName);

    const onChangeDisplayName = (e) => {
        const { target: { value } } = e;
        setDisplayName(value);
    };

    const getMyNweets = async (e) => {
        const myNweets = await dbService.collection("nweet")
            .where("creatorID", "==", userObj.uid)
            .orderBy("createdAt")
            .get();

        myNweets.docs.map((doc) => {
            console.log(doc.data());
        })
    }

    const onSubmit = async (e) => {
        e.preventDefault();

        /*
         React uses shallow equality to check for changes to the state, 
         so mutating the state will not trigger re-rendering
        */
        if (displayName !== userObj.displayName) {
            await userObj.updateProfile({
                displayName: displayName,
            })
            userObj.reloadUserObj();
        }

    }

    useEffect(() => {
        getMyNweets()
    }, [])

    return (
        <div className="container">
            <form onSubmit={onSubmit}
                className="profileForm">
                <input type="text"
                    value={displayName}
                    onChange={onChangeDisplayName}
                    className="formInput"
                    placeholder="display name" />
                <input type="submit"
                    value="update profile"
                    className="formBtn"
                    style={{
                        marginTop: 10,
                    }} />
            </form>
            <span className="formBtn cancelBtn logOut"
                onClick={onLogoutClick}>
                Log Out
            </span>
        </div>
    )
}

export default Profile;