import React, { useState } from "react"
import { dbService, storageService } from "myBase";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPencilAlt } from "@fortawesome/free-solid-svg-icons";

const Nweet = ({ nweet, isOwner }) => {
    const [editMode, setEditMode] = useState(false);
    const [newNweet, setNewNweet] = useState(nweet.text);

    const onDeleteClick = async () => {
        const confirm = window.confirm("are you sure?");
        if (confirm) {
            //delete nweet
            await dbService.doc(`nweet/${nweet.id}`).delete();
            //delete nweet related file
            await storageService.refFromURL(nweet.imgUrl).delete();
        }
    }

    const onSubmit = async (event) => {
        event.preventDefault();
        await dbService.doc(`nweet/${nweet.id}`).update({
            text: newNweet
        })
        setEditMode(false);
    }

    const onChangeEdit = (event) => {
        const { target: { value } } = event;
        setNewNweet(value);
    }

    const toggleEdit = () => {
        setEditMode(prev => !prev)
    }

    return (
        <div className="nweet">
            {editMode ?
                <>
                    <form onSubmit={onSubmit}
                        className="container nweetEdit"
                    >
                        <input type="text"
                            value={newNweet}
                            onChange={onChangeEdit}
                            required
                            autoFocus
                            className="formInput" />
                        <input type="submit"
                            value="update"
                            className="formBtn" />
                        <button onClick={toggleEdit}
                            className="formBtn cancelBtn">
                            cancel
                        </button>
                    </form>
                </>
                :
                <>
                    {nweet.imgUrl &&
                        <img src={nweet.imgUrl} />
                    }
                    <h4> {nweet.text} </h4>
                </>
            }
            {isOwner && (
                <div className="nweet__actions">
                    <span onClick={toggleEdit}>
                        <FontAwesomeIcon icon={faPencilAlt} />
                    </span>
                    <span onClick={onDeleteClick}>
                        <FontAwesomeIcon icon={faTrash} />
                    </span>
                </div>
            )
            }

        </div>
    )
}

export default Nweet;