import React, { useState } from "react";
import { dbService, storageService } from "myBase";
import { v4 as uuidv4 } from "uuid";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTimes } from "@fortawesome/free-solid-svg-icons";

const NweetFactory = ({ userObj }) => {
    const [nweet, setNweet] = useState("");
    const [nweetImg, setNweetImg] = useState("");

    const onChange = (e) => {
        const { target: { value } } = e;
        setNweet(value);
    };

    const onSubmit = async (e) => {
        if (nweet === "") {
            return;
        }
        e.preventDefault();

        let imgUrl = "";
        if (Boolean(nweetImg)) {
            //upload image file up firestore under dir named user id
            const fileRef = storageService.ref().child(`${userObj.uid}/${uuidv4()}`)
            const response = await fileRef.putString(nweetImg, "data_url")
            imgUrl = await response.ref.getDownloadURL();
        }

        const newDocument = await dbService.collection("nweet").add(
            {
                text: nweet,
                createdAt: new Date(),
                creatorID: userObj.uid,
                imgUrl
            }
        )
        setNweet("");
        setNweetImg("");
    };

    const onImageChange = (event) => {
        const { target: { files } } = event;
        const theFile = files[0];
        const fileReader = new FileReader();

        //add eventListener to fileReader
        fileReader.onloadend = (event) => {
            const { target: { result } } = event;
            setNweetImg(result)
        };

        //read file as data url
        fileReader.readAsDataURL(theFile);
    }

    return (
        <form onSubmit={onSubmit}
            className="factoryForm"
        >
            <div className="factoryInput__container">
                <input value={nweet}
                    onChange={onChange}
                    placeholder="what's on your mind?"
                    maxLength={120}
                    className="factoryInput__input"
                />
                <input type="submit"
                    value="&rarr;"
                    className="factoryInput__arrow" />
            </div>

            <label htmlFor="attach-file"
                className="factoryInput__label">
                <span>Add photos</span>
                <FontAwesomeIcon icon={faPlus} />
            </label>
            <input type="file"
                id="attach-file"
                accept="image/*"
                onChange={onImageChange}
                style={{
                    opacity: 0,
                }} />

            {nweetImg && (
                <div className="factoryForm__attachment">
                    <img src={nweetImg}
                        style={{
                            backgroundImage: nweetImg,
                        }} />
                    <div className="factoryForm__clear"
                        onClick={() => setNweetImg(null)}>
                        <span>Remove</span>
                        <FontAwesomeIcon icon={faTimes} />
                    </div>
                </div>
            )}
        </form >
    )
}

export default NweetFactory;